import { useState } from 'react'
import { useSelector } from 'react-redux'
import { verifyTwitterUser } from '../../api/user'
import { MAIN_URL } from '../../constant'
import { MDBInput } from 'mdb-react-ui-kit';

const VerifyTwitter = () => {
  const user = useSelector((state) => state.user.user)
  const accessToken = useSelector((state) => state.user.accessToken)

  const [formdata, setFormdata] = useState({
    twitterUrl: '',
  })
  const [loading, setLoading] = useState({
    verifyBtn: false,
  })

  const [dailog, setDailog] = useState({
    status: '',
    massage: '',
    active: false,
  })

  const handleChange = ({ target }) => {
    setFormdata({ ...formdata, [target.name]: target.value })
  }

  const handlePostTweet = (e) => {
    e.preventDefault()

    let twitterURL = 'https://twitter.com/intent/tweet?text='
    twitterURL += `I'm on Chilliswap`
    twitterURL += '%20%F0%9F%8C%90%0A%0A'
    twitterURL += user.publicAddress
    twitterURL += '%0A%0A'
    twitterURL += MAIN_URL + user.username ? user.username : user.publicAddress
    window.open(twitterURL, '_blank')
  }

  const handleSubmitUrl = (e) => {
    e.preventDefault()
    try {
      if(formdata.twitterUrl === ""){
        setDailog({
          status: 'error',
          massage:"invalid url",
          active: true,
        })
        return
      }
      console.log(user.username)
      if(user.username === ""){
        setDailog({
          status: 'error',
          massage:"please edit your profile",
          active: true,
        })
        return
      }
      setLoading({ ...loading, verifyBtn: true })

      let data = {
        publicAddress: user.publicAddress,
        username: user.username,
        twitter: formdata.twitterUrl,
      }
      verifyTwitterUser(accessToken, data)
      setDailog({
        status: 'success',
        massage: 'we will verify and send you a email',
        active: true,
      })
      setLoading({ ...loading, verifyBtn: false })
    //  setFormdata({...formdata, twitterUrl: ''})
    } catch (err) {
      console.log(err)
      setDailog({
        status: 'error',
        massage: err.message,
        active: true,
      })
      setLoading({ ...loading, verifyBtn: false })
    }
  }

  return (
    <section>
      <div className="container pt-5 pb-4">
            <img src="/assets/images/Group-203.svg" className="img-fluid community-left-image"/>
            <div className="row">
               <div className="col-xl-9 mx-auto background-color-contant">
                  <div className="row">
                     <div className="col-xl-7 mx-auto clr">
                      <div className="form-class add-background">

                  

                       <p>Step One</p>
                       <h3>Post a public tweet that contains your wallet address.</h3>
                       <h4 className="pt-3">Your wallet address is</h4> 
                       <h4 className="pt-2">{user.publicAddress}</h4>
                       <button onClick={handlePostTweet} className="btn btn-primary mt-3">Post Tweet</button>
                       <hr/>
                       {dailog.active ? (
                        <div className={`" p2 alert alert-${dailog.status === 'success' ? 'success' : 'danger'}`}role="alert">
                          {dailog.massage}
                        </div> ) : ( <></> )}

                        <p>Step Two</p>
                       <h3>Paste the URL of the tweet to verify your profile.</h3>
                       <div className="form-outline">
                        <MDBInput    
                            label="Tweet URL"
                            type="text"
                            placeholder="Tweet URL"
                            name="twitterUrl"
                            value={formdata.twitterUrl}
                            onChange={handleChange}
                            className="form-control"
                         />
                        {/* <label className="form-label" htmlFor="formtweeturl">Tweet URL</label> */}
                      </div>
                      {loading.verifyBtn ? (
                        <button className="btn btn-primary mt-3">Sending..</button>
                        ) : (
                        <button onClick={handleSubmitUrl} className="btn btn-primary mt-3">Confirm</button>
                        )}

                      </div>
                      
                     </div>
                     <div className="col-xl-5 contant-bg-color">
                        <img src="/assets/images/contant-bg.png" className="img-fluid grid-image"/>
                     </div>
                  </div>
               </div>
            </div>
            <img src="/assets/images/Group-29.svg" className="img-fluid community-right-image"/>
         </div>


   
    </section>
  )
}

export default VerifyTwitter
