import Skeleton, { SkeletonTheme } from 'react-loading-skeleton'

import HeaderSkeleton from './HeaderSkeleton'
import SidebarSkeleton from './SidebarSkeleton'
import ArtworkSkeleton from './ArtworkSkeleton'

const MainSkeleton = () => {
  return (
    <div>
      <HeaderSkeleton />
      <section className="product-shop spad">
        <div className="container">
          <div className="row">
            <div className="col-lg-4">
              <SidebarSkeleton />
            </div>
            <div className="col-lg-8">
              <div className="product-list">
                <div className="row">
                  <div className="col-md-12 text-center">
                    <div className="tab-content" id="nav-tabContent">
                      <div
                        className="tab-pane fade show active"
                        id="art-tab11"
                        role="tabpanel"
                        aria-labelledby="art-tab1"
                      >
                        <div className="pt-3"></div>
                        <div className="masonry1">
                            <ArtworkSkeleton key="artwork-1"/>
                            <ArtworkSkeleton key="artwork-2"/>
                            <ArtworkSkeleton key="artwork-3"/>
                            <ArtworkSkeleton key="artwork-4"/>
                            <ArtworkSkeleton key="artwork-5"/>
                            <ArtworkSkeleton key="artwork-6"/>
                        </div>
                      </div>
                    </div>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
      </section>
    </div>
  )
}
export default MainSkeleton