import { useSelector, useDispatch } from 'react-redux'
import { Link } from 'react-router-dom'
import moment from 'moment'
import { selectCreatorById, fetchUser } from '../../store/creator/creatorSlice'

// import { isVideo } from '../../utils/file'
// import Clip from '../../components/Clip'

import { IMG_BASE, ETHERSCAN_URL } from '../../constant'

import { useEffect } from 'react'

const Transction = ({ transaction }) => {
  const dispatch = useDispatch()

  let user = transaction.username
    ? transaction.username
    : transaction.from.toLowerCase()

  if (transaction.type === 'ClaimNft' || transaction.type === 'Mint') {
    user = transaction.to.toLowerCase()
  }

  const creator = useSelector((state) => selectCreatorById(state, user))

  const creatorStatus = useSelector((state) => state.creator.status)

  useEffect(() => {
    if (!creator) {
      if (creatorStatus !== 'loading') {
        dispatch(fetchUser({username:user}))
      }
    }
  }, [])

  //has to select this user
  //let description = ''
  //let avatar = creator && creator.avatar ? creator.avatar : ''
  let username = creator && creator.username ? creator.username : user
  if (transaction.type == 'Mint') {
  }

  return (
    <div className="row border-bottom pt-3">
      <div className="col-xl-7">
        <h2>
          {transaction.type} by{' '}
          <Link to={`/${username}`}>
            <span>@{username} </span>
          </Link>
        </h2>
        <p>{moment(transaction.date).format('LLL')}</p>
      </div>
      <div className="col-xl-5">
        <h2>
          {transaction.type == 'Bid' || transaction.type == 'ListOnAuction'? (
            <>{(parseInt(transaction.value) / 1e18).toFixed(4)} {transaction.currency? transaction.currency : 'ETH'}</>
          ) : (
            <></>
          )}
          <span>
            <a href={ETHERSCAN_URL + '/tx/' + transaction.hash} target="_blank">
              <img
          
                src="/assets/images/Group-arrow.svg"
                className="img-fluid"
              />
            </a>
          </span>
          {/* {(parseInt(transaction.value) / 1e18).toFixed(4)} ETH */}
        </h2>
        <p></p>
      </div>
    </div>
  )
}

export default Transction
