artworkAPI.syncAuction = async (data) => {
    return new Promise(async (resolve, reject) => {
        try {
            let response = await axios({
                url: `/api/artwork/sync/auction`,
                method: 'POST',
                data: JSON.stringify(data),
            })
            resolve(response.data)
            return
        } catch (err) {
            console.log(err)
            return reject(err.message ? err.message : 'internal error')
            
        }
    }
    )
}

artworkAPI.getMyBids = async ({accessToken}) => {
    return new Promise(async (resolve, reject) => {
        try {
            let response = await axios({
                url: `/api/artwork/my/bids`,
                headers: {
                    Authorization: `Bearer ${accessToken}`,
                  },
            })
            resolve(response.data)
            return
        } catch (err) {
            console.log(err)
            return reject(err.message ? err.message : 'internal error')
            
        }
    })
}
artworkAPI.getArtworkTransaction = async ({tokenId}) => {
    return new Promise(async (resolve, reject) => {
        try {
            let response = await axios(`/api/artwork/get/transactions/`+tokenId)
            resolve(response.data)
            return
        } catch (err) {
            console.log(err)
            return reject(err.message ? err.message : 'internal error')
            
        }
    })
}


