
import React from 'react'
import {  useDispatch } from 'react-redux'
import { Link } from 'react-router-dom'
import Clip from '../../Clip'
import { IMG_BASE } from '../../../constant'
import { isVideo } from '../../../utils'
import { EmptyAvatar } from '../../../constant'
import { logOut } from '../../../store/user/userSlice'


const UserButton = ({user,balance}) => {
  const dispatch = useDispatch()


    let { username, publicAddress, avatar } = user

    const subKey = (key)=> {
      return `${key.slice(0, 4)}...${key.slice(key.length - 4, key.length)}`
    }

    const handleLogOut = () => {
      dispatch(logOut())
    }

    avatar = avatar ? avatar : EmptyAvatar

    return (
      <>
          <ul className="topheader-ul-list">
            <li className="li-texts">
              <Link to="/">Feed</Link>
            </li>
            <li className="li-texts">
              <Link to="/bids">Bids</Link>
            </li>
          </ul>
      
          <ul className="topheader-button-list">
            <li>
              <Link to="/creator/create">
                <button className="btn btn-primary">Create</button>
              </Link>
            </li>
            <li className="dropdown">
              
                <button
                //  onClick={()=> setDropdown(!dropdown)} 
                className="btn btn-primary bid-button dropbtn">
                  <div style={{padding: '8px 0 0 8px'}} className="float-left">
                    <h2>{balance} ETH</h2>
                    <p>{subKey(publicAddress)}</p>
                  </div>
                  <div className="float-right">
                    {isVideo(avatar) ? (
                      <Clip
                        className="img-fluid user-img"
                        url={IMG_BASE + avatar}
                      />
                    ) : (
                      <img
                        className="img-fluid user-img"
                        src={IMG_BASE + avatar}
                      />
                    )}
                  </div>
                </button>
              

      
                  <>
                  <div className="dropdown-content">
                  <div className="row">
                    <div className="col-xl-12 text-center">
                      {isVideo(avatar) ? (
                        <Clip className="img-fluid" url={IMG_BASE + avatar} />
                      ) : (
                        <img className="img-fluid" src={IMG_BASE + avatar} />
                      )}
                      <p>{username}</p>
                    </div>
                  </div>
                  <Link to={`/${username ? username : publicAddress}`}>
                    <button type="button" className="btn btn-success btn-sm">
                      Visit Profile
                    </button>
                  </Link>
                 <a href="#">
                 <button
                    onClick={handleLogOut}
                    className="btn btn-success btn-sm" >
                    Log Out
                  </button>
                 </a>
                </div>
                {/* <div  onClick={()=> setDropdown(false)} className="backdoor"></div> */}
                </>
                
        
              

            </li>
          </ul>
       
      </>
    )
  }

  export default UserButton