import React, { useEffect, useState } from 'react'
import { useSelector, useDispatch } from 'react-redux'
import CreatorCard from '../../components/Card/CreatorCard'
import {creatorAPI} from '../../api/creator'
import {selectCreators} from '../../store/creator/creatorSlice'
const Creators = (props) => {

  const {searchCreators} = creatorAPI
  //const selectedCreators = useSelector((state) =>selectCreators(state))
  //console.log(selectedCreators)
  const [creators, setCreators] = useState([])
  const [searchValue, setSearchValue] = useState('')
  // const [isSearching, setIsSearching] = useState({
  //   status:false,
  //   queue: false
  // })

  useEffect(()=>{
    fetchCreators()
  },[])

  const onSearchHandle = (e)=> {
    let value = e.target.value

    setSearchValue(value)
    fetchCreators(value)
    // if(isSearching.status){
    //   console.log("bujsy")
    //   setIsSearching({...isSearching,queue: true})
    // }else{
    //   fetchCreators()
    // }
  }


  
  const fetchCreators = async (value)=>{
    try{
     // setIsSearching({...isSearching, status: true})
      let data = {
        search: value
      }
      let searchedCreators = await searchCreators(data)
      setCreators(searchedCreators)
    //  setIsSearching({...isSearching, status:false})
    }catch(err){
      console.log(err)
      // setIsSearching({ status:false,
      //   queue: false})
    }
    
  }

  return(
    <>
        <div className="container pb-6 pt-5">
         <div className="row">
            <div className="col-xl-12">
               <div className="form-group has-search">
                  <span className="form-control-feedback">
                    <img src="assets/images/all-creators-search-icon.svg" className="img-fluid"/></span>
                  <input type="text" 
                   className="form-control"
                   placeholder="Search for a creator..."
                   value={searchValue}
                   onChange={onSearchHandle}
                   />
               </div>
            </div>
         </div>
      </div>

      <section className="features">
         <div className="container">
            <div className="row">

            {creators && creators.map((creator) => {
                  if (creator.username != '') {
                    return (
                      <div key={creator._id} className="col-lg-4 pb-4">
                        <CreatorCard creator={creator} />
                      </div>
                    )
                  }
                })}

              {creators && creators.length <= 0? (<>
                <div className="no-creators-containter">
                  <h3>No Creators</h3>
                </div>
              </>):(<></>) }

              </div>
              </div>
              </section>
    </>
  )

}

export default Creators