import React, {useState, useEffect} from 'react'
import { Link } from 'react-router-dom'
import { useSelector } from 'react-redux'
import { selectMyBids } from '../../store/artwork/artworkSlice'
import Artwork from './artwork'

import {artworkAPI} from '../../api/artwork'

import Loading from '../../components/Loading'
import ArtworkSkeleton from './Skeleton/Bids'

const Bids = () => {

  const {getMyBids} = artworkAPI

  const userSlice = useSelector((state) => state.user)
  //const myBids = useSelector((state) => selectMyBids(state))
  const [myBids , setMyBids] = useState([])
  const [isLoading, setIsLoading] = useState(true)

  useEffect(()=>{
    
    ;(async()=>{

      let artworks = await getMyBids({accessToken: userSlice.accessToken})
      artworks = artworks.map((artwork)=>{
        let bidders = artwork.bidders
        
        if (bidders.length > 0) {
          let myLastBid = bidders[bidders.length - 1]
          artwork.myBid = myLastBid
          artwork.status = 'Winning'
          if (artwork.owner !== userSlice.user.publicAddress) {
            artwork.status = 'Outbid'
          }
          return  artwork //artwork.push(artwork)
        }

      })

      setMyBids(artworks)
      setIsLoading(false)

    })().catch((err)=>{
      console.log(err)
      setIsLoading(false)
    })

  },[])


  const RenderBids = () => {
    if(isLoading){
      return (
        <>
        <ArtworkSkeleton/>
        <ArtworkSkeleton/>
        </>
      )
    }else if(myBids.length <= 0 ){
      return (
        <div className="col-xl-12 col-lg-12 col-md-12 col-sm-12 col-12">
        <div style={{textAlign: 'center', padding: '20%'}} className="user-no-record-found">
          <h3 className="user-title">You have not bid on any auctions yet! </h3>
          <p className="theme-description">browse and buy your artwork today</p>
          <div className="user-btn">
            <Link to="/artworks" className="btn btn-warning">Browse</Link>
          </div>
        </div>
      </div>
      )
    }else{
      return myBids.map((bid) => {
        return <Artwork key={bid._id} bid={bid} user={userSlice.user} />
      })
    }
  }



  return (
    <div className="container pt-5 pb-4">
      <div className="row">
        <div className="col-xl-12">
          <h4 className="text-color font-weight-bold">My bids</h4>
        </div>

      {RenderBids()}
        
      </div>
    </div>
  )
}

export default Bids
