import Skeleton, { SkeletonTheme } from 'react-loading-skeleton'

const CreatorsSkeleton = () => {

    const Creator = ()=> {
        return(
            <div  className="card" >
            <div className="card-img-top">
            <Skeleton height={300} />
            </div>
            <div className="img-fluid creator-image-bige creator-avatar-img">
            <Skeleton />
            </div>
      
            <div className="card-body">
              <h4 className="card-title"><Skeleton /></h4>
              <h6 className="font-weight-bold mt-4 pb-2"><Skeleton /></h6>
              <p className="card-text pt-3"><Skeleton /></p>
              <div className="know-more border-top">
                <div className="float-left mt-3">
                  <button className="btn btn-primary">Follow</button>
                </div>
                <div className="float-right mt-3">
                  <h3> <Skeleton /></h3>
                  <p> Followers</p>
                </div>
              </div>
            </div>
          </div>
       
  
        )
    }


  return (
    <>
      {[...Array(3)].map((x, i) => (
        <div key={"ceator-"+i} className="col-lg-4">
          <SkeletonTheme color="#dcdedd" highlightColor="#fff">
         <Creator key={i} />
         </SkeletonTheme>

        </div>
      )
      )}
     </>
  )
}

export default CreatorsSkeleton
