
import  Skeleton, { SkeletonTheme }  from 'react-loading-skeleton';

const CoverSkeleton = ()=>{
    return(
        <SkeletonTheme color="#dcdedd" highlightColor="#fff">

        <div  className="hero-items">
           <div className="single-hero-items set-bg">
               <div className="container">
                 <div className="row">
                   <div className="col-lg-6 p-4 col-12 order-lg-2 order-1">
                     <div className="banner-image">
                      <Skeleton height={400} />
                     </div>
                   </div>
                   <div className="col-lg-6 pt-5 col-12 order-lg-1 order-2">
                     <span className="text-color">    <Skeleton /> </span>
                     <h1 className="text-color font-weight-bold">
                     <Skeleton />
                     </h1>
                     <p>Artwork information</p>
                     <div className="row">
                       <div className="col-xl-10 banner-counter">
                          <div className="row">
                             <div className="col-xl-6 border-right p-4"><div>
                                <h3>Current Bid</h3>
                             </div>
                             <h2 className="font-weight-bold"><Skeleton /> </h2>
                             <h6 className="text-color pt-2"> <Skeleton /></h6>
                           </div>
                           <div className="col-xl-6">
                           <div className="counter">
                               <h3 className="pb-2 pl-2">Auction ending in</h3>
                               <div id="countdown1">
                                 <div className="well d-flex">
                                 <Skeleton height={40} />
                                 </div>
                               </div>
                             </div>
                           </div>
                         </div>


                       </div>
                       <div className="col-xl-6 tabs-section pt-5">
         
                       <Skeleton  height={40} />
                       </div>
                     </div>
                   </div>
                 </div>
                 <div className="off-card floating">
                   <img src="/assets/images/Group-29.svg" />
                 </div>
               </div>
             </div>
   </div>
   </SkeletonTheme>

    )
 
}

export default CoverSkeleton