import React from 'react'
import { Link } from 'react-router-dom'
import {useSelector}from 'react-redux'
import { selectCreatorById } from '../../store/creator/creatorSlice'

import Clip from '../Clip'
import Countdown from '../Countdown'

import { IMG_BASE } from '../../constant'
import { isVideo } from '../../utils'

import EmptyAvatar from '../../images/avatar.png'

const ArtworkCard = ({ artwork }) => {
  const RenderMedia = (artwork, title) => {
    if (isVideo(artwork)) {
      return (
        <div className="media">
          <Clip url={IMG_BASE + artwork} className="video" />
        </div>
      )
    } else {
      return (
        <div className="media">
          <img className="img" src={IMG_BASE + artwork} title={title} />
        </div>
      )
    }
  }

  const RenderCreator = (creatorId) => {
    const creator = useSelector((state) => selectCreatorById(state, creatorId))

    const avatar = creator && creator.avatar ? IMG_BASE+ creator.avatar : EmptyAvatar
    const username = creator && creator.username ? creator.username : 'unknown'
    return (
      <>
        <div className="col-xl-12 pb-1 col-12">
          <img src={ avatar} className="img-fluid  creator-image " />
        </div>
        <div className="col-xl-12 pb-1 float-left col-12">
          <p className="item-content-p" >@{username}</p>
        </div>
      </>
    )
  }

  const RenderAutionDetails = (price, isAuction,currency) => {
    let value = price / 1e18

    return (
      <div className="col-xl-6 col-6">
      <h5>{isAuction ? 'Current Bid' : 'Sold for'}</h5>
        <h6>{value} {currency}</h6>
      </div>
    )
  }

  const renderTimmer = (isAuction, endTime) => {
    if (isAuction) {
      let dif = parseInt(endTime) - parseInt(Math.floor(Date.now() / 1000))
        return (
          <div className="col-xl-6 col-6">
          <h5>Ending In</h5>
          {dif > 0? (<Countdown type="mini-countdown" endTime={endTime} />) : (<h6>Auction has Ended</h6>) }
        </div>
        

        )
      
    }
  }

  const renderDescription= (description)=> {
    try{
      if(!description){
        return ''
      }
      let text = JSON.parse(description)
      text = text.content
      text = text[0].children[0].text
      return text
    }catch(err){
      return description
    }
   
  }

  return (
    <Link
      to={`/${artwork.username ? artwork.username : artwork.creator}/${
        artwork.url
      }`}
      className="item"
    >
      {RenderMedia(artwork.artwork, artwork.title)}

      <div className="row iten-content">
        {RenderCreator(artwork.creator)}

        <div className="col-xl-12 border-top">
          <h4>{artwork.title}</h4>
          <p>{(renderDescription(artwork.description))}...
          </p>
        </div>
       {RenderAutionDetails(artwork.price, artwork.isAuction, artwork.currency )}
      {renderTimmer(artwork.isAuction,artwork.endTime )}
        

      </div>
    </Link>
  )
}

export default ArtworkCard
