import { Link } from 'react-router-dom'

import Category from '../category'
import Logo from '../logo'
import UserButton from './user'


const Mobile  = ({user, balance,  onClickConnect, handleLogOut})=>{


    return(
        <header className="header-section  mobile-view">
          <div className="container">
            <div className="row">
              <div className="col-lg-8 col-md-8 col-10">
                <Logo/>
              </div>
     
  <div className="col-lg-4 col-md-4 col-2">
      <div className="mobile-view-nav">
          <input type="checkbox" id="openSidebarMenu" />
          <label htmlFor="openSidebarMenu" className="sidebarIconToggle">
            <div className="spinner top"></div>
            <div className="spinner middle"></div>
            <div className="spinner bottom"></div>
          </label>
        <div id="sidebarMenu">
          <ul className="menu">
            <div>
              <div className="container">
              {user ? (
              <UserButton user={user} balance={balance}/>
            ) : (
              <div className="col-lg-4 col-md-4 col-12">
                <button onClick={onClickConnect} className="btn btn-danger center mobile-connect-button">CONNECT</button>
              </div>
            )}
            
             </div>
                    </div>

                    <Category type="mboile"/>
                </ul>
            </div>
            <div id="mobile-menu-wrap"></div>
        </div>
   
  </div>
</div>
</div>
  </header>
 
    )
}

export default Mobile