import {Link} from 'react-router-dom'
const JoinCommunity = ()=>{

    return(
        <section className="join-the-community">
            <div className="container pt-5 pb-4">
            <div className="row">
            <div className="col-xl-6 text-left">
                <h1 className="text-color">Join the Community Upvote to get access.</h1>
                <p className="mt-5 text-color">With Community Upvote, artists are encouraged to support other artists \nd to set the stage for a model of community-led curation that puts power in the hands of creators. In the new creative economy, we all rise up together.</p>
                <Link to="/introduction/community"><button className="btn btn-primary mt-3">View the Community Upvote</button></Link>
            </div>
            <div className="col-xl-6 width-control" >
                <img src="/assets/images/component-img-imp.svg" className=""/>
            </div>
            </div>
            <img src="/assets/images/Group-29.svg" className="img-fluid community-right-image"/> 
            </div>
        </section>

    )

}

export default JoinCommunity