import Skeleton, { SkeletonTheme } from 'react-loading-skeleton'

const BidSkeleton = () => {

  return (
  
<div className="col-xl-12">
<SkeletonTheme color="#dcdedd" highlightColor="#fff">
      <div className="row bids-list">
         <div className="col-xl-3">
         <Skeleton height={200} />

      </div>
         <div className="col-xl-9 bids-sub-carry">
            <div className="row">
               <div className="col-xl-4 border-bottom">
                  <div className="row">
                  <div className="col-xl-8 col-8 md-6">
                     <p> <Skeleton/></p>
                  </div>
               </div>
               </div>
               <div className="col-xl-8 border-bottom">
                  <h5> <Skeleton/></h5>
               </div>
               
               <div className="col-xl-12">
                  <div className="row">
                     <div className="col-xl-3">
                        <div className="bids-list-grid-1">
                        <h4>My bid</h4>
                        <h3 className="font-weight-bold text-color"> <Skeleton/></h3>
                     </div>
                     </div>
                     <div className="col-xl-3">
                         <div className="bids-list-grid-2">
                        <h4>Current Bid</h4>
                        <h3 className="font-weight-bold text-color"> <Skeleton/></h3>
                        {/* <h6 className="text-color pt-2">$1,070.70</h6> */}
                     </div>
                     </div>
                     <div className="col-xl-3">
                        <div className="bids-list-grid-3">
                        <div className="counter">
                           <h4>Auction ending in</h4>
                           <div id="countdown1">
                              <div className="well d-flex">
                              <Skeleton/>
                              </div>
                           </div>
                        </div>
                     </div>
                     </div>
                     <div className="col-xl-3">
                          <div className="bids-list-grid-4 pl-4 i-am-wating-align">
                
                              <h3 className="text-color font-weight-bold"> <Skeleton/></h3>


                     </div>
                     </div>
                  </div>
               </div>
            </div>
         </div>
      </div>
      </SkeletonTheme>

   </div> 
  )
}

export default BidSkeleton
