import { createSlice, createAsyncThunk } from '@reduxjs/toolkit'
import { artworkAPI } from '../../api/artwork'

const initialState = {
  artworks: [],
  error: null,
  status: 'idle',
}

export const fetchAllArtworks = createAsyncThunk('artwork/fetchAllArtworks', async () => {
    let artworks = await artworkAPI.fetchAllArtwork()
    return artworks
  },
)

export const fetchArtwork = createAsyncThunk( 'artwork/fetchArtwork', async (tokenId) => {
    let artwork = await artworkAPI.fetchArtwork(tokenId)
    return artwork
  },
)


export const syncAuction = createAsyncThunk( 'artwork/syncAuction', async (data, {rejectWithValue  }) => {
  try {
    console.log("sycning")
      let artwork = await artworkAPI.syncAuction(data)
      return artwork

  } catch (err) {
    return rejectWithValue(null, err);
  }

},
)

const artworkSlice = createSlice({
  name: 'artwork',
  initialState,
  reducers: {},
  extraReducers: {
    [fetchAllArtworks.pending]: (state, action) => {
      state.status = 'loading'
    },
    [fetchAllArtworks.fulfilled]: (state, action) => {
      state.status = 'succeeded'
      state.artworks = action.payload
    },
    [fetchAllArtworks.rejected]: (state, action) => {
      state.status = 'failed'
      state.error = action.error.message
    },
    [fetchArtwork.pending]: (state, action) => {
      state.status = 'loading'
    },
    [fetchArtwork.fulfilled]: (state, action) => {
      state.status = 'succeeded'
      state.artworks = [...state.artworks, action.payload]
    },
    [fetchArtwork.rejected]: (state, action) => {
      state.status = 'failed'
      state.error = action.error.message
    },
    [syncAuction.pending]: (state, action) => {
      state.status = 'loading'
    },
    [syncAuction.fulfilled]: (state, action) => {
      state.status = 'succeeded'
      let artwork = action.payload
      const index = state.artworks.findIndex(
        (e) => e.tokenId === artwork.tokenId,
      )
      state.artworks[index] = action.payload
    },
    [syncAuction.rejected]: (state, action) => {
      state.status = 'failed'
      state.error = action.error.message
    },
  },
})

export default artworkSlice.reducer

export const selectArtworkById = (state, artworkId) =>{
  let tokenId = parseInt(artworkId)
  if(isNaN(tokenId)){
    return state.artwork.artworks.find((artwork) => artwork.url === artworkId)
  }else{
    return state.artwork.artworks.find((artwork) => artwork.tokenId === artworkId)

  }
}
  
export const selectArtworkByTokenId = (state, artworkId) =>
  state.artwork.artworks.find((artwork) => artwork.tokenId === artworkId)


export const selectLiveArtworks = (state) =>{
  let artworks = state.artwork.artworks
  return artworks.length > 0? artworks.filter((artwork) => artwork.isAuction === true) : []
  
}
  
export const selectSoldArtworks = (state) =>{
  let artworks = state.artwork.artworks
  return artworks.length > 0? artworks.filter((artwork) => artwork.isAuction === false) : []}
  

export const selectMyBids = (state) => {
  const user = state.user.user
  if (!user) {
    return []
  }
  let artworks = state.artwork.artworks
  let myBiddedArtworks = []

  artworks.forEach((artwork) => {
    if(!artwork.isAuction){
      return
    }
    let myBiddedArtwork = { ...artwork }
    let bidders = myBiddedArtwork.bidders
    let myBids = bidders.filter(
      (bidder) => bidder.bidder === user.publicAddress,
    )
    if (myBids.length > 0) {
      let myLastBid = myBids[myBids.length - 1]
      myBiddedArtwork.myBid = myLastBid
      myBiddedArtwork.status = 'Winning'
      if (myBiddedArtwork.owner !== user.publicAddress) {
        myBiddedArtwork.status = 'Outbid'
      }
      myBiddedArtworks.push(myBiddedArtwork)
    }
  })
  return myBiddedArtworks
}
