import Skeleton, { SkeletonTheme } from 'react-loading-skeleton'

const ArtworkSkeleton = () => {

    const Artwork = ()=> {
        return(

            <div className="item">
            <div className="media">
              <div style={{ maxWidth: '100%' }}>
                <Skeleton height={200}  />
              </div>
            </div>
  
            <div className="row iten-content">
              <div className="col-xl-12 pb-1 col-12">
                <Skeleton circle={true} height={50} width={50} />
              </div>
              <div className="col-xl-12 pb-1 float-left col-12">
                <p className="item-content-p">
                  <Skeleton />
                </p>
              </div>
              <div className="col-xl-12 border-top">
                <h4>
                  <Skeleton />
                </h4>
                <p>  <Skeleton /></p>
              </div>
              <div className="col-xl-6 col-6">
                <h5>
                  <Skeleton />
                </h5>
                <h6>
                  <Skeleton />
                </h6>
              </div>
  
              <div className="col-xl-6 col-6">
                <h5>Ending In</h5>
                <h6>
                  <Skeleton />
                </h6>
              </div>
            </div>
          </div>
       
  
        )
    }


  return (
    <SkeletonTheme color="#dcdedd" highlightColor="#fff">
      <div className="masonry">

      {[...Array(10)].map((x, i) => 
      <Artwork key={i} /> )}
     
      </div>
    </SkeletonTheme>
  )
}

export default ArtworkSkeleton
