import React, { useEffect, useState } from 'react'
import { useHistory, Link } from 'react-router-dom'
import { useSelector, useDispatch } from 'react-redux'

import Loading from '../../components/Loading'
import Clip from '../../components/Clip'
import Countdown from '../../components/Countdown'
import MetaMask from '../../components/MetaMask'

import { placeBet } from '../../services/Web3/token'
import { getCurrentBalance } from '../../services/Web3/web3'
import { getCurrentTokenBalance } from '../../services/Web3/token'
import {
  selectArtworkById,
  syncAuction,
} from '../../store/artwork/artworkSlice'
import { selectCreatorById } from '../../store/creator/creatorSlice'

import { isVideo } from '../../utils/'
import { IMG_BASE, LEAST_BET,EmptyAvatar } from '../../constant'

const Bid = (props) => {
  const dispatch = useDispatch()
  const history = useHistory()

  const { creator: creatorUsername, artwork: artworkId } = props.match.params
  const artwork = useSelector((state) => selectArtworkById(state, artworkId))
  const creator = useSelector((state) =>
    selectCreatorById(state, creatorUsername),
  )
  const userSlice = useSelector((state) => state.user)

  const [formData, setFormData] = useState({
    token: 0,
    value: 0,
    leastBid: 0,
  })

  const [balance, setBalance] = useState({
    ether: 0,
    token: 0
  })

  const [buttonState, setButtonState] = useState({
    bid: false,
  })

  const [dailog, setDailog] = useState({
    active: false,
    status: '',
    message: '',
  })

  const [showMetaMaskModal, setShowMetaMaskModal] = useState(false)

  const onClickConnect = () => {
    setShowMetaMaskModal(true)
  }

  const closeMetaMaskModal = () => {
    setShowMetaMaskModal(false)
  }




  const handleChange = ({ target }) => {
    setFormData({ ...formData, [target.name]: target.value })
    setDailog({
      active: false,
      status: '',
      message: '',
    })
  }

  useEffect(() => {
    try {
      if (artwork) {

        let currentTime = parseInt(Math.floor(Date.now() / 1000))
        let endTime = parseInt(artwork.endTime)
        if((artwork.bidders).length > 0){
          if(isNaN(endTime) || currentTime > endTime){
            history.push(`/${artwork.username}/${artwork.url}`)
          }
        }
        
        let price = parseFloat(artwork.price)
        price = price + (price * LEAST_BET) / 100
        price = price / 1e18
        price = parseFloat(price).toFixed(6)
        setFormData({ ...formData, leastBid: price })
      }
    } catch (err) {
      console.log(err)
    }
  }, [artwork])

  useEffect(() => {
    try {
      if (userSlice.user) {
        ;(async () => {
          let balance = await getCurrentBalance()
          let tokenBalance = await getCurrentTokenBalance(userSlice.user.publicAddress)
          balance = parseFloat(balance).toFixed(6)
          tokenBalance = parseFloat(tokenBalance).toFixed(2)
          setBalance({ ...balance, ether: balance , token:tokenBalance })
        })()
      }
    } catch (err) {
      console.log(err)
    }
  }, [userSlice.user])

  const handlePlaceBid = async (e) => {
    e.preventDefault()
    try {
      if (!userSlice.user) {
        setDailog({
          active: true,
          status: 'error',
          message: 'need to login in first',
        })
        return
      }
      setButtonState({ ...buttonState, bid: true })
      let value = parseFloat(formData.value)
      if (value <= 0 && value < formData.leastBid) {
        setDailog({
          active: true,
          status: 'error',
          message: 'value must be greater than the least bid',
        })
        setButtonState({ ...buttonState, bid: false })
        return
      }
      if (artwork.currency === "ETH") {
        if(balance.ether < value){
          setDailog({
            active: true,
            status: 'error',
            message: 'insufficent balance',
          })
          setButtonState({ ...buttonState, bid: false })
          return
        }    
      }else{
        if(balance.token < value){
          setDailog({
            active: true,
            status: 'error',
            message: 'insufficent balance',
          })
          setButtonState({ ...buttonState, bid: false })
          return
        }  
      }

      let bidedTx = await placeBet(artwork.tokenId, value, artwork.currency)

     // dispatch(syncAuction({ tokenId: artwork.tokenId }))
      // setButtonState({ ...buttonState, bid: false })
      setTimeout(()=>{
        history.push('/bids')
      },4000)
      
    } catch (err) {
      console.log(err)
      setDailog({
        active: true,
        status: 'error',
        message: 'invalid value',
      })
      setButtonState({ ...buttonState, bid: false })

      return
    }
  }

  if (!artwork) {
    // wait for artowrk slice and if its not in the slice then request for it in database
    return <Loading />
  }

  return (
    <section className="place-bid-details">
      <div className="container pt-5 pb-4">
        <img
          src="/assets/images/Group-203.svg"
          className="img-fluid community-left-image"
        />

        <div className="row pt-5">
          <div className="col-xl-6">
            <div className="place-bid-left-section">
              <Link to={`/${artwork.username}/${artwork.url}`}>
              {isVideo(artwork.artwork) ? (
                <Clip className="img-fluid" url={IMG_BASE + artwork.artwork} />
              ) : (
                <img src={IMG_BASE + artwork.artwork} className="img-fluid" />
              )}
              </Link>
            </div>
            <div className="row desc-class p-4">
              <div className="col-xl-6 border-right">
                <h2>{artwork.title}</h2>
              </div>
              <div className="col-xl-6">
                <div className="row">
                  <div className="col-xl-4 col-4">
                  <Link to={`/${artwork.username}`}>

                    {isVideo(creator.avatar) ? (
                      <Clip
                        className="img-fluid"
                        url={IMG_BASE + creator.avatar}
                      />
                    ) : (
                      <img
                        src={IMG_BASE + (creator.avatar ? creator.avatar : EmptyAvatar)}
                        className="img-fluid"
                      />
                    )}
                    </Link>
                  </div>
                  <div className="col-xl-8 col-8">
                  <Link to={`/${artwork.username}`}>
                    <p>@{artwork.username}</p>
                    </Link>
                  </div>
                </div>
              </div>
            </div>
          </div>
          <div className="col-xl-6">
            <div className="place-bid-right-section">
              <div className="row">
                {/* <button
                  type="button"
                  className="btn btn-warning font-weight-bold place-a-bid-button"
                >
                  Place a Bid
                </button> */}
                <div className="col-xl-12 banner-counter">
                  <div className="row">
                    <div className="col-xl-12 border-bottom">
                      <div className="row first-grid">
                        <div className="col-xl-6 col-6">
                          {dailog.active ? (
                            <div className="dailog-containter">
                              <div
                                className={`alert alert-${
                                  dailog.status == 'error'
                                    ? 'danger'
                                    : 'success'
                                }`}
                              >
                                {dailog.message}
                              </div>
                            </div>
                          ) : (
                            <div></div>
                          )}

                          <p className="text-color">Your Bid</p>
                        </div>
                        <div className="col-xl-4 col-4">
                          {/* <p className="text-color">$1,070.70</p> */}
                        </div>
                      </div>
                      <div className="row pt-3">
                        <div className="col-xl-8 col-8">
                          <div className="form-group">
                            <input
                              type="number"
                              className="form-control"
                              name="value"
                              value={formData.value}
                              placeholder="Enter your bet"
                              pattern="[0-9]{0,5}"
                              onChange={handleChange}
                            />
                          </div>
                        </div>
                        <div className="col-xl-4 col-4">
                          <h4>{artwork.currency}</h4>
                        </div>
                      </div>
                    </div>
                    <div className="col-xl-6 border-right p-4 col-12">
                      <a >
                        <h3>You must bid at least</h3>
                      </a>
                      <h2 className="font-weight-bold">
                        {formData.leastBid} {artwork.currency}
                      </h2>
                      {/* <h6 className="text-color pt-2">$0</h6> */}
                    </div>
                    <div className="col-xl-6 col-12">
                      <div className="counter">
                        <h3 className="pb-2 pl-2">Auction ending in</h3>
                        <div id="countdown1">
                          <div className="well d-flex">
                            <Countdown endTime={artwork.endTime} />
                          </div>
                        </div>
                      </div>
                    </div>
                  </div>
                </div>
              </div>

              {userSlice.user ? (
                <div className="place-bid-right-bottom">
                  <div className="row">
                    <div className="col-xl-8 mx-auto">
                      <div className="row">
                        <div className="col-xl-6 col-6">
                          <button type="button" className="btn btn-info">
                            Your Balance
                          </button>
                        </div>
                        <div className="col-xl-6 col-6">
                          <button type="button" className="btn btn-secondary">
                            {artwork.currency=="ETH"? (`${balance.ether} ${artwork.currency}`):(`${balance.token} ${artwork.currency}`)}
                            
                          </button>
                        </div>
                      </div>
                    </div>
                  </div>
                  <div className="col-xl-12 pt-3 text-center">
                    {buttonState.bid ? (
                      <button className="btn btn-warning">
                        Bidding.. Please wait
                      </button>
                    ) : (
                      <button
                        onClick={handlePlaceBid}
                        type="button"
                        className="btn btn-warning"
                      >
                        Place Your Bid
                      </button>
                    )}
                  </div>
                  <div className="col-xl-12 pt-3">
                    <p className="text-center">
                      Bids placed in an auction cannot be withdrawn. Learn how
                      our auctions work.
                    </p>
                  </div>
                </div>
              ) : (
                <div>
                  <div className="pt-3">
                    <button onClick={onClickConnect} className="btn btn-primary">Connect to metamask</button>
                  </div>
                </div>
              )}
            </div>
          </div>
        </div>
      </div>
      <MetaMask
      showModal={showMetaMaskModal}
      handleClose={closeMetaMaskModal}
    />
    </section>
  )
}
export default Bid
