import React, { useEffect, useState } from 'react'
import { Link, useHistory } from 'react-router-dom'
import { useSelector, useDispatch } from 'react-redux'

import { selectCreatorById,updateCreators } from '../../store/creator/creatorSlice'

import ArtworkCard from '../../components/Card/ArtworkCard'

import Sidebar from './sidebar'
import Header from './header'

import MainSkeleton from './Skeleton/MainSkeleton'
import ArtworkSkeleton from './Skeleton/ArtworkSkeleton'

import { creatorAPI } from '../../api/creator'
import { artworkAPI } from '../../api/artwork'


const Creator = (props) => {
  const dispatch = useDispatch()
  const history = useHistory()

  const { creator: creatorUsername } = props.match.params

  const creator = useSelector((state) => selectCreatorById(state, creatorUsername))

  const [activeTab, setActiveTab] = useState('Collection')
  const [loading, setLoading] = useState(true)
  const [artworks, setArtworks] = useState({
    collection: [],
    creation: [],
  })
 
  const creatorStatus = useSelector((state) => state.creator.status)

  useEffect(() => {
    ;(async () => {
      if (!creator && creatorStatus === 'succeeded') {
        let fetchedCreator = await creatorAPI.fetchUser({
          username: creatorUsername,
        })
        if(!fetchedCreator){
          history.push('/')
          return
        }
        dispatch(updateCreators(fetchedCreator))

      //  fetchedCreator ? setCreator(fetchedCreator) : history.push('/')
      }
    })()
  }, [creator, creatorStatus])

  useEffect(() => {
    ;(async () => {
      if (creator) {
        try {
          let fetchedCollection = await artworkAPI.fetcUserCollections(
            creator.publicAddress,
          )
          let fetchedCreation = await artworkAPI.fetchUserCreations(
            creator.username ? creator.username : creator.publicAddress,
          )
          setArtworks({
            collection: fetchedCollection,
            creation: fetchedCreation,
          })
          // if no colelction the active tab does to Creation
          if (fetchedCollection.length <= 0) {
            setActiveTab('Creation')
          }
          setLoading(false)
        } catch (err) {
          console.log(err)
          setLoading(false)

          //set errro
        }
      }
    })()
  }, [creator])

  const handleTab = (e) => {
    e.preventDefault()
    let tab = e.target.dataset.value
    setActiveTab(tab)
  }

  const Tab = ({ name }) => {
    return (
      <a
        className={`nav-item nav-link ${activeTab === name ? 'active' : ''}`}
        onClick={handleTab}
        data-value={name}
      >
        {name}
      </a>
    )
  }

  const renderTabs = () => {
    if (!loading) {
      let collection = artworks.collection
      let creation = artworks.creation

      if (creation.length <= 0 && collection.length <= 0) {
        return (
          <div className="center-container">
            <div className="big-text">Your collection is empty.</div>
            <div>
              Start building your collection by placing bids on artwork.
            </div>
          </div>
        )
      }

      return (
        <nav className="nav-justified ">
          <div className="nav nav-tabs " role="tablist">
            {collection.length > 0 ? <Tab name="Collection" /> : <div></div>}
            {creation.length > 0 ? <Tab name="Creation" /> : <div></div>}
          </div>
        </nav>
      )
    }
  }

  const renderArworks = () => {
    let collection = artworks.collection
    let creation = artworks.creation
    if(loading){
      return(
        <>
        <ArtworkSkeleton  key="artwork-1"/>
        <ArtworkSkeleton  key="artwork-2"/>
        <ArtworkSkeleton  key="artwork-3"/>
        <ArtworkSkeleton  key="artwork-4"/>
        <ArtworkSkeleton  key="artwork-5"/>
        <ArtworkSkeleton  key="artwork-6"/>
        </>
      )
    }
    if (activeTab === 'Collection') {
      return collection.map((artwork) => {
        return <ArtworkCard key={artwork._id} artwork={artwork} />
      })
    } else if (activeTab === 'Creation') {
      return creation.map((artwork) => {
        return <ArtworkCard key={artwork._id} artwork={artwork} />
      })
    }

  }


  if(!creator ){
    return <MainSkeleton />
  }

  return (
    
    <div>
 
    <Header creator={creator}/>

      <section className="product-shop spad">
        <div className="container">
          <div className="row">
            <div className="col-lg-4">
              <Sidebar creator={creator} />
            </div>
            <div className="col-lg-8">
              <div className="product-list">
                <div className="row">
                  <div className="col-md-12 text-center">
                    {renderTabs()}

                    <div className="tab-content" id="nav-tabContent">
                      <div
                        className="tab-pane fade show active"
                        id="art-tab11"
                        role="tabpanel"
                        aria-labelledby="art-tab1"
                      >
                        <div className="pt-3"></div>
                        <div className="masonry1">
                        {renderArworks()}
                        </div>
                      </div>
                    </div>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
      </section>
    </div>
  )
}

export default Creator
