import React, { useState, useEffect } from 'react'
import { useHistory } from 'react-router-dom'
import { useSelector, useDispatch } from 'react-redux'
import { MDBInput } from 'mdb-react-ui-kit';

import Clip from '../../components/Clip'
import MyEditor from '../../components/Editor'

import { mintToken } from '../../services/Web3/token'
import ipfs from '../../services/ipfs'
import { isVideo, readImg } from '../../utils'
import { fetchAllArtworks } from '../../store/artwork/artworkSlice'

import NoImage from '../../images/No-Image.jpg'

const categories = [
  'Fine Arts',
  'Celebrities',
  'Relics',
  'Sports',
  'Agencies',
  'Artist',
  'Trading Cards',
  'Virtual Worlds',
]

const initialEditorValue = [
  {
    type: 'paragraph',
    children: [{ text: '' }],
  },
]

const Mint = (props) => {
  const dispatch = useDispatch()
  let history = useHistory()

  const userSlice = useSelector((state) => state.user)

  const [progress, setProgress] = useState('idle')

  const [imgData, setImgData] = useState({
    fileType: 'image',
    imgData: '',
    imgUrl: NoImage,
  })

  const [editorValue, setEditorValue] = useState(initialEditorValue)

  const [formData, setFormData] = useState({
    title: '',
    description: '',
    artwork: '',
    metadata: '',
    category: 'Fine Arts',
    royalty: '0'
  })

  const [dailog, setDailog] = useState({
    active: false,
    status: '',
    message: '',
  })

  useEffect(() => {
    
    if (userSlice.status == 'succeeded' && userSlice.user.role != 'creator') {
      history.push('/join/community')
    }
  }, [userSlice])

  const handleChange = ({ target }) => {
    setFormData({ ...formData, [target.name]: target.value })
    setDailog({
      active: false,
      status: '',
      message: '',
    })
  }

  const handleImgUpload = async (e) => {
    e.stopPropagation()
    e.preventDefault()
    try {
      const file = e.target.files[0]
      const name = e.target.files[0].name
      let fileType = 'image'

      let imgUrl = await readImg(file, 'read')
      let imgData = await readImg(file, 'data')

      const imgBuffer = await Buffer.from(imgData)

      if (isVideo(name)) {
        fileType = 'video'
      }

      setImgData({
        imgUrl: imgUrl,
        imgData: imgBuffer,
        fileType: fileType,
      })
      setProgress('file')
    } catch (err) {
      console.log(err)
    }
  }

  const uploadArtwork = async () => {
    try {
      console.log('uploading nft img')

      if (progress === 'uploading') {
        return false
      }
      setProgress('uploading')
      setDailog({
        active: false,
        status: '',
        message: '',
      })

      let path = '/nft.jpg'
      if (imgData.fileType == 'video') {
        path = '/nft.mp4'
      }

      let ipfsImgResponse = await ipfs.add(
        {
          path: path,
          content: imgData.imgData,
        },
        { wrapWithDirectory: true },
      )

      let artworkHash = ipfsImgResponse[1].hash
      artworkHash = artworkHash + path

      setFormData({ ...formData, artwork: artworkHash })

      setDailog({
        active: true,
        status: 'success',
        message: 'uploaded artwork to ipfs successfully',
      })
      setProgress('uploaded')

      // const uploadedArtwork = await createArtwork(userSlice.accessToken, {
      //   artworkHash,
      // })
      //   console.log(uploadedArtwork)
    } catch (err) {
      console.log(err)
      setFormData({ ...formData, artwork: '' })
      setProgress('file')
      setDailog({
        active: true,
        status: 'error',
        message: err.message,
      })
    }
  }

  const handleMintNFT = async (e) => {
    e.preventDefault()
    try {
      console.log('minting')

      if (formData.title === '' || formData.artwork === '') {
        throw { message: 'Fill the Form' }
      }

      setProgress('minting')

      let description = {
        editor: 'slate',
        content: editorValue,
      }
      description = JSON.stringify(description)
      const metaData = JSON.stringify({
        title: formData.title,
        description: description,
        artwork: formData.artwork,
        category: formData.category,
      })

      let ipfsmetaDataResponse = await ipfs.add(Buffer.from(metaData))
      let metadataHash = ipfsmetaDataResponse[0].hash
      let mintedNFT = await mintToken(metadataHash, formData.artwork, formData.royalty)

      // fetch the artwork again in reduers
      dispatch(fetchAllArtworks())

      setProgress('uploaded')

      history.push(
        '/creator/list/' + mintedNFT.events.Mint.returnValues.tokenId,
      )
    } catch (err) {
      console.log(err)
      setProgress('uploaded')
      setDailog({
        active: true,
        status: 'error',
        message: err.message,
      })
    }
  }

  // const RenderButton = () => {
  //   if (progress === 'uploaded') {
  //     return <button className="btn btn-primary">Uploaded to IPFS</button>
  //   } else if (progress === 'uploading') {
  //     return (
  //       <button className="btn btn-primary">Uploading.. Please wait!</button>
  //     )
  //   } else if (progress === 'file') {
  //     return (
  //       <button className="btn btn-primary" onClick={uploadArtwork}>
  //         Upload to IPFS
  //       </button>
  //     )
  //   } else {
  //     return (
  //       <input
  //         className="uploadFileBtn"
  //         name="artwork"
  //         id="artworkInput"
  //         type="file"
  //         onChange={handleImgUpload}
  //       ></input>
  //     )
  //   }
  // }

  return (
    <section className="join-the-community community-section-background">
      <div className="container pt-5 pb-4">
        <div className="row">
          <div className="col-xl-12 text-left mx-auto">
            <div className="row">
              <div className="col-xl-6 text-left">
                <div className="custum-ul-li-design">
                  <h1 className="title3">How it works</h1>
                  <ul>
                    <li>
                      <img src="/assets/images/hexa01.png" className="" />
                      Upload your NFT artwork
                    </li>
                    <li>
                      <img src="/assets/images/hexa02.png" className="" />
                      Click on Upload to IPFS button
                    </li>
                    <li>
                      <img src="/assets/images/hexa03.png" className="" /> Artwork hash would be auto genarated
                    </li>
                    <li>
                      <img src="/assets/images/hexa04.png" className="" /> Fill up the title and description
                    </li>
                    <li>
                      <img src="/assets/images/hexa05.png" className="" /> Click on Mint NFT button
                    </li>
                  </ul>
                </div>
              </div>

              <div className="col-lg-6 text-center">
                <div className="row">
                  <div className="col-xl-4">
                    <div className="upload">
                      {imgData.fileType == 'video' ? (
                        <Clip url={imgData.imgUrl} className="video" />
                      ) : (
                        <img className="img" src={imgData.imgUrl}/>
                      )}

                      {/* <h3>No Image</h3> */}
                    </div>
                  </div>
                  <div className="col-xl-12 pt-3">
                    <div className="form-group">
                      <input
                        type="file"
                        className="form-control-file"
                        name="artwork"
                        id="artworkInput"
                        accept="image/*,video/*,audio/*"
                        onChange={handleImgUpload}
                      />
                    </div>
                  </div>
                  <div className="col-xl-12 mb-5">
                  {progress === 'uploading'?(<button className="btn btn-primary upload-mar">Uploading...</button>):
                  (<button className="btn btn-primary upload-mar" onClick={uploadArtwork}>Upload to IPFS</button>)}
                  </div>
                </div>

                <div>
                  <div className="col-lg-12">
                  {dailog.active ? (
                  <div className="dailog-containter">
                    <div className={`alert alert-${dailog.status === 'error'? 'danger': 'success'}`}>
                      {dailog.message}
                    </div>
                  </div>
                ) : (
                  <></>
                )}
                    </div>
                  <div className="col-lg-12">
                    <div className="form-outline mb-4">
                      <MDBInput
                        required
                        readOnly
                        name="artwork"
                        value={formData.artwork}
                        onChange={handleChange}
                        type="text"
                        className="form-control"
                        placeholder="Artwork Hash code"
                        label=" Artwork Hash"

                      />
                      {/* <label className="form-label" htmlFor="formart">
                        Artwork Hash
                      </label> */}
                    </div>
                  </div>
                  <div className="col-lg-12">
                    <div className="form-outline mb-4">
                      <MDBInput
                        type="text"
                        className="form-control"
                        required
                        name="title"
                        placeholder="Title"
                        value={formData.title}
                        onChange={handleChange}
                        label="Title"
                      />
                      {/* <label className="form-label" htmlFor="fortitle">
                        Title
                      </label> */}
                    </div>
                  </div>

                  <div className="col-lg-12">
                    <div className="form-outline mb-4">
                    <select
                      type="text"
                      className="form-control"
                      required
                      name="category"
                      placeholder="category"
                      value={formData.category}
                      onChange={handleChange}
                      
                    >
                      {categories &&
                        categories.map((category) => {
                          return <option key={category}>{category}</option>
                        })}
                    </select>
                    {/* <label className="form-label" htmlFor="fortitle">
                    Category
                  </label> */}
                  </div>
                </div>

                  <div className="col-lg-12">
                    <div className="form-outline mb-4">
                    <MyEditor value={editorValue} setValue={setEditorValue} />
                      <label className="form-label" htmlFor="fordescription">
                        Description
                      </label>
                    </div>
                  </div>
                  <div className="form-group row pt-3">
                    <div className="col-sm-12 text-center">
                    {progress === 'minting' ? (
                      <button className="btn btn-primary">Minting...</button>
                    ):(
                      <button onClick={handleMintNFT} className="btn btn-primary">Mint NFT</button>
                    )}
                      
                    </div>
                  </div>
              </div>

            </div>

          </div>
        </div>
      </div>
      </div>
    </section>
  )
}
export default Mint
