import Skeleton, { SkeletonTheme } from 'react-loading-skeleton'

const ArtworkSkeleton = () => {

  return (
    <SkeletonTheme color="#dcdedd" highlightColor="#fff">

<section>
        <div className="container">
          <div className="row">
            <div className="col-xl-5">
              <div className="about-image">
                  <Skeleton height={500} />
              </div>
            </div>
            <div className="col-xl-6 pb-4">
              <div className="about-info">
                <div className="row pb-3">
                  <div className="col-lg-12 about-grid">
                    <span className="text-color font-weight-bold">
                     <Skeleton/>
                    </span>
                    <h2 className="text-color font-weight-bold"><Skeleton/></h2>
                  </div>
                </div>
                <h1><Skeleton/></h1>
                <p><Skeleton/></p>

                <div className="col-xl-12 banner-counter mt-5">
                <Skeleton height={200}/>
                </div>
              </div>
            </div>
          </div>
        </div>
      </section>

    </SkeletonTheme>
  )
}

export default ArtworkSkeleton
