
import React, { useEffect, useState, useRef } from 'react'
import { Overlay, Tooltip } from 'react-bootstrap'

import AvatarImg from '../../images/avatar.png'
import CoverImg from '../../images/cover.jpg'
import Clip from '../../components/Clip'
import { isVideo } from '../../utils'
import {IMG_BASE,MAIN_URL } from '../../constant'

const Header = ({creator})=> {
    const coverImg = creator.coverImg ? IMG_BASE + creator.coverImg : CoverImg
    const avatar = creator.avatar ? IMG_BASE + creator.avatar : AvatarImg


    const [reportPopUp, setReportPopUp] = useState(false)

    const [shareTooltip, setShareTooltip] = useState(false)
    const shareTarget = useRef(null)

    const handleShare = ()=>{
      let shareLink = `${MAIN_URL}${creator.username? creator.username: creator.publicAddress}`

      setShareTooltip(true)
      navigator.clipboard.writeText(shareLink)
      setTimeout(() => {
        setShareTooltip(false)
      }, 1000)
      
    }

    return(
        <div className="breacrumb-section" style={{backgroundImage:`url(${coverImg})`}}>
        <div className="container">
          <div className="row">
            <div className="col-lg-6">
              <div className="breadcrumb-image">
                {isVideo(creator.avatar) ? (
                  <Clip url={avatar} />
                ) : (
                  <img className="img-fluid" src={avatar} />
                )}
              </div>
            </div>
            <div className="col-lg-6">
            <div className="breadcrumb-text">
              {reportPopUp?(
                 <div className="report-pop-up">
                   <p>Report</p>
                 </div>
              ):(<></>)}
             
                <img onClick={()=> setReportPopUp(!reportPopUp)} src="assets/images/threedots.png" className="img-fluid" />

                <button ref={shareTarget} onClick={handleShare} className="btn btn-primary">Share</button>
                <Overlay
                target={shareTarget.current}
                show={shareTooltip}
                placement="top"
              >
                {(props) => (
                  <Tooltip id="overlay-share-tooltip" {...props}>
                    Copied
                  </Tooltip>
                )}
              </Overlay>
              </div>
            </div>
          </div>
        </div>
      </div>
    )
}

export default Header