import React, {useState} from 'react'
import { Link } from 'react-router-dom'
import { claimNft } from '../../services/Web3/token'

import Countdown from '../../components/Countdown'
import Clip from '../../components/Clip'

import { IMG_BASE } from '../../constant'
import { isVideo } from '../../utils'

  const Artwork = ({ bid ,user}) => {

    let currentTime = parseInt(Math.floor(Date.now() / 1000))

    
    const handleNFTClaim = async (event) => {
        event.preventDefault()
        try {
          const tokenId = event.target.value.toString()
          let claimedNft = await claimNft(tokenId)
          console.log(claimedNft)
        } catch (err) {
          console.log(err)
        }
      }

    return (
      <div className="col-xl-12">
      <div className="row bids-list">
         <div className="col-xl-3">
         {isVideo(bid.artwork) ? (
              <Clip
                className="img-fluid grid-image align-grid-image"
                url={IMG_BASE + bid.artwork}
              />
            ) : (
              <img
                className="img-fluid grid-image align-grid-image"
                src={IMG_BASE + bid.artwork}
              />
            )}

            {/* <img src="assets/images/gallery/1.jpg" className="img-fluid grid-image align-grid-image"/> */}
         </div>
         <div className="col-xl-9 bids-sub-carry">
            <div className="row">
               <div className="col-xl-4 border-bottom">
                  <div className="row">
                  {/* <div className="col-xl-4 col-4">
                     <img src="assets/images/creator.png" className="img-fluid web-view-creator-image"/>
                  </div> */}
                  <div className="col-xl-8 col-8 md-6">
                     <Link to={`/${bid.username}`}><p>@{bid.username}</p></Link>
                  </div>
               </div>
               </div>
               <div className="col-xl-8 border-bottom">
                  <h5>{bid.title}</h5>
               </div>
               
               <div className="col-xl-12">
                  <div className="row">
                     <div className="col-xl-3">
                        <div className="bids-list-grid-1">
                        <h4>My bid</h4>
                        <h3 className="font-weight-bold text-color">  {bid.myBid.value / 1e18} {bid.currency}</h3>
                        {/* <h6 className="text-color pt-2">$1,070.70</h6> */}
                     </div>
                     </div>
                     <div className="col-xl-3">
                         <div className="bids-list-grid-2">
                        <h4>Current Bid</h4>
                        <h3 className="font-weight-bold text-color">{bid.price / 1e18} {bid.currency}</h3>
                        {/* <h6 className="text-color pt-2">$1,070.70</h6> */}
                     </div>
                     </div>
                     <div className="col-xl-3">
                        <div className="bids-list-grid-3">
                        <div className="counter">
                           <h4>Auction ending in</h4>
                           <div id="countdown1">
                              <div className="well d-flex">
                              <Countdown endTime={bid.endTime} />
                              </div>
                           </div>
                        </div>
                     </div>
                     </div>
                     <div className="col-xl-3">
                          <div className="bids-list-grid-4 pl-4 i-am-wating-align">
                     {/* <button type="button" className="btn btn-warning">
                       <img src="assets/images/edit-your-bid.svg" className="img-fluid "/>Edit your bid</button> */}
                              <h3 className="text-color font-weight-bold">I am {bid.status}</h3>

                       {currentTime > parseInt(bid.endTime) && bid.owner ===  user.publicAddress? 
                       (<button className="btn btn-warning" value={bid.tokenId} onClick={handleNFTClaim}>
                         <img src="/assets/images/data-collecting.svg" className="img-fluid "/>
                         Collect
                       </button>) :
                        (<></> )}
                        
                        {bid.status == 'Outbid' ? (
                          <Link to={`${bid.username == ""?bid.publicAddress: bid.username}/${bid.url}/bid`} className="btn btn-warning ">
                            <img src="/assets/images/edit-your-bid.svg" className="img-fluid"/>
                            Bid Again
                          </Link>
                       
                      ) : (
                          <Link to={`${bid.username == ""?bid.publicAddress: bid.username}/${bid.url}`} className="btn btn-warning ml-2">
                            View
                          </Link>
                      )}

                     </div>
                     </div>
                  </div>
               </div>
            </div>
         </div>
      </div>
   </div> )
  }


  export default Artwork