import React, {useState} from 'react'
import { Link } from 'react-router-dom'
import { useSelector, useDispatch } from 'react-redux'
import moment from 'moment'
import { followCreator } from '../../api/user'
import { updateCreators } from '../../store/creator/creatorSlice'
import { updateUser } from '../../store/user/userSlice'

const Sidebar = ({ creator }) => {
  const dispatch = useDispatch()

  const accessToken = useSelector((state) => state.user.accessToken)
  const user = useSelector((state) => state.user.user)

  const [loadingFollow, setLoadingFollow] = useState(false)

  const handleFollow = async () => {
    try {
      setLoadingFollow(true)
      let data = {
        creator: creator.publicAddress,
      }
      let response = await followCreator(accessToken, data)
      dispatch(updateCreators(response.creator))
      dispatch(updateUser(response.user))
      setLoadingFollow(false)

    } catch (err) {
      console.log(err)
      setLoadingFollow(false)

    }
  }

  const getSubKey = (key) => {
    return `${key.slice(0, 5)}...${key.slice(key.length - 5, key.length)}`
  }

  const getUsername = ()=> {
    if(creator){
      let username = creator.username
      if( username === ""){
        username = (creator.publicAddress).slice(0,8) + '...'
        username = username.toUpperCase()
      }
      return username
    }
    return ""
   
  }

  const FollowButton = ({ user, creator }) => {
    let isFollowing = false
    if (user) {
      let following = user.following
      following.forEach((_following) => {
        if (_following === creator.publicAddress) {
          isFollowing = true
        }
      })
      if(loadingFollow){
        return (
          <button className="btn btn-primary"> Loading... </button>
        )
      }
      return (
        <button onClick={handleFollow} className="btn btn-primary">
          {isFollowing ? 'Unfollow' : 'Follow'}
        </button>
      )
    }
    return <></>
  }

  return (
    <div className="produts-sidebar p-4">
      <div className="filter-widget pb-4 first-grid">
        <div className="row">
          <div className="col-xl-6">
            <button
              style={{ textTransform: 'uppercase' }}
              type="button"
              className="btn btn-warning"
            >
              #{creator._id.slice(0, 5)}
            </button>
          </div>
          <div className="col-xl-6">
            <h5 className="pl-2 pt-3 border-top uppercase">
              {getSubKey(creator.publicAddress)}
            </h5>
            <hr />
          </div>
        </div>
      </div>

      <div className="filter-widget desc-section">
        <h4 style={{}} className="black-color">{creator.fullname}</h4>
          <h6 className="black-color pb-4 pt-3">@{getUsername()}</h6>
        <div className="row">
          {user? (
            <div className="col-xl-4 col-12">
            {user && user.publicAddress === creator.publicAddress ? (
              <Link style={{ textDecoration: 'none' }} to="/profile/edit">
                <button className="btn btn-primary">Edit</button>
              </Link>
            ) : (
              <FollowButton user={user} creator={creator} />
            )}</div>
          ):(<></>)}

          <div className="col-xl-4 col-6">
            <div className="pl-2">
              <strong>{creator.following.length}</strong>
              <p>Following</p>
            </div>
          </div>
          <div className="col-xl-4 col-6">
            <strong>{creator.followers.length}</strong>
            <p>Followers</p>
          </div>
          
          <div className="col-xl-8">
              <div className="verify-twitter1">
                <div className="form-group">
                  <input type="text" className="form-control" readOnly placeholder={getUsername()}/>
                       <img src="/assets/images/at.png" className="img-fluid border-right"/>
               </div>
               </div>
          </div>
         
        </div>
      </div>
      <hr />
      {creator.bio !== '' ? (
        <>
          <div className="filter-widget">
            <h6>Bio</h6>
            <hr />
            <p>{creator.bio}</p>
          </div>
          <hr />{' '}
        </>
      ) : (
        <></>
      )}

      <div className="filter-widget">
        <strong className="pb-2">Links</strong>

        <ul className="links">
          {creator.instagram ? (
            <li>
              <a href={creator.instagram} target="_blank">
                <img
                  src="/assets/images/social-media-icons/Group-296.svg"
                  className="pr-2"
                />
                {creator.instagram}
              </a>
            </li>
          ) : (
            <></>
          )}

          {creator.youtube ? (
            <li>
              <a href={creator.youtube} target="_blank">
                <img
                  src="/assets/images/social-media-icons/Path-172.svg"
                  className="pr-2"
                />
                {creator.youtube}
              </a>
            </li>
          ) : (
            <></>
          )}

          {creator.facebook ? (
            <li>
              <a href={creator.facebook} target="_blank">
                <img
                  src="/assets/images/social-media-icons/Path-173.svg"
                  className="pr-2"
                />
                {creator.facebook}
              </a>
            </li>
          ) : (
            <></>
          )}

          {creator.tiktok ? (
            <li>
              <a href={creator.tiktok} target="_blank">
                <img
                  src="/assets/images/social-media-icons/Path-174.svg"
                  className="pr-2"
                />
                {creator.tiktok}
              </a>
            </li>
          ) : (
            <></>
          )}
        </ul>
        <hr />
      </div>

      <div className="filter-widget">
        <div className="row">
          <div className="col-xl-6 col-6">
            <h6>Joined</h6>
          </div>
          <div className="col-xl-6 col-6">
            <h6>{moment(creator.date).format('LL')}</h6>
          </div>
        </div>
      </div>
      <hr />
    </div>
  )
}

export default Sidebar
