import { useEffect, useState } from "react";
import { useSelector } from "react-redux";
import { Link } from "react-router-dom";
//import Carousel from 'react-bootstrap/Carousel'
import OwlCarousel from "react-owl-carousel";

import { selectLiveArtworks } from "../../store/artwork/artworkSlice";
import Countdown from "../../components/Countdown";
import Clip from "../../components/Clip";
import CoverSkeleton from "./Skeleton/CoverSkeleton";

import { IMG_BASE } from "../../constant";
import { isVideo } from "../../utils";
import { getETHRate } from "../../api/setting";

let ETH_RATE = 2082.87;

const Cover = () => {
  const [videoSound, setVideoSound] = useState(false);
  const handleVideoSound = () => {
    setVideoSound(!videoSound);
  };
  const artworks = useSelector((state) => selectLiveArtworks(state));
  let coverArtworks = artworks;
  if (coverArtworks.length > 3) {
    coverArtworks = coverArtworks.slice(0, 3);
  }

  useEffect(() => {
    (async () => {
      try {
        let response = await getETHRate();
        ETH_RATE = parseFloat(response.price);
      } catch (err) {
        console.log(err);
      }
    })();
  }, []);

  return (
    <section className="hero-section">
      {coverArtworks.length >= 1 ? (
        <OwlCarousel
          loop
          nav
          items={1}
          navText={[
            '<i class="fa fa-angle-left"></i>',
            '<i class="fa fa-angle-right"></i>',
          ]}
          smartSpeed={1200}
          autoPlay
          className="hero-items owl-carousel"
        >
          {coverArtworks &&
            coverArtworks.map((artwork, i) => {
              return (
                <div
                  key={"cover-id" + artwork._id + i}
                  className="single-hero-items set-bg"
                >
                  <div className="container">
                    <div className="row">
                      <div className="col-lg-6 p-4 col-12 order-lg-2 order-1">
                        <div className="banner-image">
                          {isVideo(artwork.artwork) ? (
                            <>
                              <Clip
                                className="img-fluid"
                                url={IMG_BASE + artwork.artwork}
                                onLoad={handleVideoSound}
                                sound={videoSound}
                              />
                            </>
                          ) : (
                            <img
                              className="img-fluid"
                              src={IMG_BASE + artwork.artwork}
                            />
                          )}
                        </div>
                      </div>
                      <div className="col-lg-6 pt-5 col-12 order-lg-1 order-2">
                        <span className="text-color">@{artwork.username}</span>
                        <h1 className="text-color font-weight-bold">
                          {artwork.title}
                        </h1>
                        <p>Artwork information</p>
                        <div className="row">
                          <div className="col-xl-10 banner-counter">
                            <div className="row">
                              <div className="col-xl-6 border-right p-4">
                                <div>
                                  <h3>Current Bid</h3>
                                </div>
                                <h2 className="font-weight-bold">
                                  {artwork.price / 1e18} {artwork.currency}
                                </h2>
                                <h6 className="text-color pt-2">
                                  $
                                  {((artwork.price / 1e18) * ETH_RATE).toFixed(
                                    2
                                  )}
                                </h6>
                              </div>
                              <div className="col-xl-6">
                                <div className="counter">
                                  <h3 className="pb-2 pl-2">
                                    Auction ending in
                                  </h3>
                                  <div id="countdown1">
                                    <div className="well d-flex">
                                      <Countdown endTime={artwork.endTime} />
                                    </div>
                                  </div>
                                </div>
                              </div>
                            </div>
                          </div>
                          <div className="col-xl-6 tabs-section pt-5">
                            <Link
                              to={`/${
                                artwork.username
                                  ? artwork.username
                                  : artwork.creator
                              }/${artwork.url}/bid`}
                            >
                              <button
                                type="button"
                                className="nav-link active"
                                style={{
                                  margin: "0px auto",
                                  width: "50%",
                                  float: "left",
                                  border: "1px solid #FDC644",
                                }}
                              >
                                Place a Bid
                              </button>
                            </Link>
                            <Link
                              to={`/${
                                artwork.username
                                  ? artwork.username
                                  : artwork.creator
                              }/${artwork.url}`}
                            >
                              {" "}
                              <button
                                type="button"
                                className="nav-link "
                                style={{
                                  margin: "0px auto",
                                  width: "50%",
                                  float: "left",
                                  border: "1px solid #FDC644",
                                }}
                              >
                                View Details
                              </button>
                            </Link>
                          </div>
                        </div>
                      </div>
                    </div>
                    <div className="off-card floating">
                      <img src="/assets/images/Group-29.svg" />
                    </div>
                  </div>
                </div>
              );
            })}
        </OwlCarousel>
      ) : (
        <CoverSkeleton />
      )}
    </section>
  );
};

export default Cover;
