import React from 'react'
import { useSelector } from 'react-redux'
import {Link} from 'react-router-dom'
import CreatorCard from '../../components/Card/CreatorCard'

import { selectCreators } from '../../store/creator/creatorSlice'
import CreatorsSkeleton from './Skeleton/CreatorsSkeleton'

const Creators = () => {
  let creators = useSelector((state) => selectCreators(state))
  creators = creators.slice(0, 3)

  return (
    <section className="features">
      <div className="container">
        <div className="row">
          <div className="col-lg-12 pb-4">
            <h2 className="title1 float-left">Featured creators</h2>
            <Link to="/creators">
            <button className="btn btn-primary float-right">
             View all creators
            </button>
            </Link>
          </div>
          {creators && creators.length >= 1 ? (
            <>
            {
              creators.map((creator, i) => {
                return (
                  <div key={"ceator-"+creator._id} className="col-lg-4">
                    <CreatorCard creator={creator} />
                  </div>
                )
              })}
              </>

          ):(<CreatorsSkeleton/>)}

          
        </div>
      </div>
    </section>
  )
}

export default Creators
