
import React from 'react'
import {  useDispatch } from 'react-redux'
import { Link } from 'react-router-dom'
import Clip from '../../Clip'
import { IMG_BASE } from '../../../constant'
import { isVideo } from '../../../utils'
import { EmptyAvatar } from '../../../constant'
import { logOut } from '../../../store/user/userSlice'


const UserButton = ({user,balance}) => {
  const dispatch = useDispatch()


    let { username, publicAddress, avatar } = user

    const subKey = (key)=> {
      return `${key.slice(0, 4)}...${key.slice(key.length - 4, key.length)}`
    }

    const handleLogOut = () => {
      dispatch(logOut())
    }

    avatar = avatar ? avatar : EmptyAvatar

    return (
      <>
    <div className="row">
              <div className="col-8 rmv_pding">
                  <div className="dropdown1">
                        <ul>
                          <li className="bck_clr">FEED</li>
                          <li className="bck_clr">BIDS</li>
                          <li className="bck_clr"><a href="" className="side-menu-create">Create</a></li>
                        </ul>
                    </div>
                  </div>
                  <div className="col-4 rmv_pding">
                    <div className="dropdown">
                        <button className="dropbtn">{balance} ETH
                            <div className="">
                                <span>{subKey(publicAddress)}</span>
                              </div>
                        </button>
                        <div className="dropdown-content">
                            <a href="#">
                                {isVideo(avatar) ? (
                                <Clip
                                    className="img-fluid user-img"
                                    url={IMG_BASE + avatar}
                                />
                                ) : (
                                <img
                                    className="img-fluid user-img"
                                    src={IMG_BASE + avatar}
                                />
                                )}
                            </a>
                            <a href="#">{username}</a>
                            <Link to={`/${username ? username : publicAddress}`}>
                                <button type="button" className="btn btn-success btn-sm">Visit Profile</button>
                            </Link>
                            <a href="#"><button onClick={handleLogOut} className="btn btn-success btn-sm">Log Out</button></a>
                        </div>
                      </div>
                    </div>
                </div>
      </>
    )
  }

  export default UserButton