import React, { useEffect, useState, useRef } from 'react'
import { useDispatch, useSelector } from 'react-redux'
import { Link } from 'react-router-dom'
import { FullScreen, useFullScreenHandle } from 'react-full-screen'
import { Overlay, Tooltip } from 'react-bootstrap'
import Clip from '../../components/Clip'
import Loading from '../../components/Loading'
import EditorText from '../../components/Editor/EditorText'
import { isVideo } from '../../utils'

import FullScreenImg from '../../images/fullscreen.png'
import MinimizeImg from '../../images/minimize.png'
import MuteImg from '../../images/mute.png'
import UnmuteImg from '../../images/unmute.png'

import ArtworkSkeleton from './Skeleton/ArtworkSkeleton'

import {
  selectArtworkById,
  fetchArtwork,
} from '../../store/artwork/artworkSlice'
import { selectCreatorById } from '../../store/creator/creatorSlice'

import { IMG_BASE, TOKEN_URI, EmptyAvatar, MAIN_URL } from '../../constant'

//import ArtworkDetails from './ArtworkDetails'
import AuctionDetails from './AuctionDetails'
import ArtworkHistory from './ArtworkHistory'


const Artwork = (props) => {
  const dispatch = useDispatch()
  const handleFullScreen = useFullScreenHandle()

  const { creator: creatorUsername, artwork: artworkId } = props.match.params
  const artwork = useSelector((state) => selectArtworkById(state, artworkId))
  const creator = useSelector((state) =>
    selectCreatorById(state, creatorUsername),
  )
  const error = useSelector((state) => state.artwork.error)

  //const [artwork, setArtwork] = useState(selectedArtwork)
  const [videoSound, setVideoSound] = useState(false)
  const [shareTooltip, setShareTooltip] = useState(false)
  const shareTarget = useRef(null)

  const handleVideoSound = ()=> {
    setVideoSound(!videoSound)
  }

  if (error) {
    console.log('ERROR: ' + error)
  }

  useEffect(() => {
    if (!artwork) {
      dispatch(fetchArtwork(artworkId))
    }
  }, [artwork])

  if (!artwork) {
    return <ArtworkSkeleton/>
  }

  const handleShare = (e) => {
    let shareLink = `${MAIN_URL}${artwork.username}/${artwork.url}`
    let name = e.target.dataset.name
    console.log(name)
    if (name === 'twitter') {
      let text = 'https://twitter.com/intent/tweet'
      text =
        text +
        `?text=Check out this artwork by ${artwork.username} on @withCHILLISWAP %0A%0A`
      text = text + shareLink
      window.open(text, '_blank')
    } else {
      setShareTooltip(true)
      navigator.clipboard.writeText(shareLink)
      setTimeout(() => {
        setShareTooltip(false)
      }, 1000)
    }
  }

  const renderDescription = (description) => {
    try {
      let text = JSON.parse(description)
      return <EditorText text={text.content} />
    } catch (err) {
      return description
    }
  }

  return (
    <>

      <section>
        <div className="container">
          <div className="row">
            <div className="col-xl-5">
              <div className="about-image">
                <FullScreen handle={handleFullScreen}>
                  <div>
                    {isVideo(artwork.artwork) ? (
                      <>
                      <Clip
                        className="img-fluid"
                        url={IMG_BASE + artwork.artwork}
                        sound={videoSound}
                      />
                    <div onClick={handleVideoSound} style={{ right: '90px'}}  className="rounded-svg"  >
                      <img src={ videoSound? UnmuteImg: MuteImg } />
                    </div>
                      </>
                    ) : (
                      <img
                        className="img-fluid"
                        src={IMG_BASE + artwork.artwork}
                      />
                    )}

               
                    <div
                      onClick={
                        handleFullScreen.active
                          ? handleFullScreen.exit
                          : handleFullScreen.enter
                      }
                      className="rounded-svg"
                    >
                      <img
                        src={
                          handleFullScreen.active ? MinimizeImg : FullScreenImg
                        }
                      />
                    </div>
                  </div>
                </FullScreen>
              </div>
            </div>
            <div className="col-xl-6 pb-4">
              <div className="about-info">
                <div className="row pb-3">
                  <div className="col-lg-12 about-grid">
                    {' '}
                    <span className="text-color font-weight-bold">
                      @{artwork.username}
                    </span>
                    <h2 className="text-color font-weight-bold">{artwork.title}</h2>
                  </div>
                </div>
                <h1>Artwork information</h1>
                <p>{renderDescription(artwork.description)}</p>

                <div className="col-xl-12 banner-counter mt-5">
                  <AuctionDetails artwork={artwork} />
                </div>
              </div>
            </div>
          </div>
        </div>
      </section>

      <section>
        <div className="container">
          <div className="row">
            <div className="col-xl-12 share-section pb-4">
              <button
                ref={shareTarget}
                onClick={handleShare}
                type="button"
                className="btn btn-warning float-left"
              >
                <img src="/assets/images/Solid.svg" className="img-fluid" />
                Share
              </button>

              <Overlay
                target={shareTarget.current}
                show={shareTooltip}
                placement="top"
              >
                {(props) => (
                  <Tooltip id="overlay-share-tooltip" {...props}>
                    Copied
                  </Tooltip>
                )}
              </Overlay>
              <div className="share-buttons float-left">
                  <a href="#" data-name="twitter" onClick={handleShare}><i data-name="twitter" className="fa fa-twitter"></i></a>
                  {/* <a href="#" data-name="facebook" onClick={handleShare}><i data-name="facebook" className="fa fa-facebook"></i></a>
                  <a href="#" data-name="instagram" onClick={handleShare}><i  data-name="instagram" className="fa fa-instagram"></i></a> */}
              </div>

            </div>
          </div>
          <div className="row">
            <div className="col-lg-12 pb-4">
              <h2 className="title1 float-left">History</h2>
            </div>
          </div>

          <div className="row">
            <div className="col-lg-6">
              <ArtworkHistory artwork={artwork} />
            </div>

            <div className="col-xl-4 offset-xl-2 history-buttons pt-5">
              <div className="row">
                <div className="col-xl-12">
                  <a href={TOKEN_URI + artwork.tokenId} target="_blank">
                    <button type="button" className="btn btn-warning">
                      View on Etherscan
                      <img
                        src="/assets/images/etherscan-logo-light-circle.png"
                        className="img-fluid"
                      />
                    </button>
                  </a>
                </div>
                <div className="col-xl-12">
                  <a href={IMG_BASE + artwork.metadata} target="_blank">
                    <button type="button" className="btn btn-success">
                      View on IPFS
                      <img
                        src="/assets/images/IPFS.png"
                        className="img-fluid"
                      />
                    </button>
                  </a>
                </div>
    
              </div>
            </div>
          </div>
        </div>
      </section>

      {creator ? (
        <section>
          <div className="container">
            <div className="row">
              <div className="col-xl-12 border-bottom">
                <h4 className="text-color font-weight-bold pb-2">Creator</h4>
              </div>
            </div>
            <div className="row pt-4">
              <div className="col-xl-4 border-right">
                <div className="row">
                  <div className="col-xl-4">
                    <Link
                      to={`/${
                        creator.username
                          ? creator.username
                          : creator.publicAddress
                      }`}
                    >
                      <img
                        src={
                          creator.avatar
                            ? IMG_BASE + creator.avatar
                            : IMG_BASE + EmptyAvatar
                        }
                        className="img-fluid"
                      />
                    </Link>
                  </div>
                  <div className="col-xl-8 p-30">
                    <h4 className="font-weight-bold text-color">
                      {creator.fullname}
                    </h4>
                    <h6 className="font-weight-bold text-color">
                      {/* <Link to={`/${creator.username? creator.username:creator.publicAddress }`}> */}
                      @
                      {creator.username
                        ? creator.username
                        : creator.publicAddress.slice(0, 6)}
                      {/* </Link> */}
                    </h6>
                  </div>
                </div>
              </div>
              <div className="col-xl-8">
                <h5 className="creator-text text-color">{creator.bio}</h5>{' '}
              </div>
            </div>
          </div>
        </section>
      ) : (
        <></>
      )}
    </>

  )
}
export default Artwork
