import React, { useState, useEffect } from 'react'
import { useSelector, useDispatch } from 'react-redux'
import {Link} from 'react-router-dom'
import { syncAuction } from '../../store/artwork/artworkSlice'
import Countdown from '../../components/Countdown'
import {getETHRate} from '../../api/setting'

import { getAuction } from '../../services/Web3/token'

import AuctionFooter from './AuctionFooter'

let ETH_RATE = 2082.87

const AuctionDetails = ({ artwork }) => {
  const dispatch = useDispatch()

  const artworkSlice = useSelector((state) => state.artwork)

  const [artworkAuction, setArtworkAuction] = useState({
    active: false,
    seller: '0',
    endTime: '0',
    owner: '0',
    value: '0',
  })

  
  useEffect(()=>{

    ;(async()=>{
      let response = await getETHRate()
      ETH_RATE = parseFloat(response.price)   

    })().catch((err)=>{
      console.log(err)
    })

    ;(async()=>{
     if (artwork) {
        setArtworkAuction({
          active: artwork.isAuction,
          seller: artwork.seller,
          value: artwork.price,
          endTime: artwork.endTime,
          owner: artwork.owner,
        })

        if(artworkSlice.status !== 'loading'){
          let {isSync} = await verifyAuction(artwork)
          console.log(isSync)
          if(!isSync){
            dispatch(syncAuction({ tokenId: artwork.tokenId }))
          }
        }
      }
    })().catch((err)=>{
      console.log(err)
    })
    

  },[])

  // useEffect(() => {
  //  ;(async()=>{
 
  //  })()
    
  // }, [artwork])


  const verifyAuction = (artwork) => {
    return new Promise(async(resolve, reject)=>{
      //{ active, seller, value, endTime, owner, currency } 
    try{
      let auction = await getAuction(artwork.tokenId)

      if(!artwork.title || !artwork.description){
        resolve({isSync: false})
        return
      }


      if (auction.active) {
        if (artworkSlice.status !== 'loading') {
        if (auction.value !== artwork.price || auction.endTime !== artwork.endTime || auction.currency  !== artwork.currency ) {
          resolve({isSync: false})
          return
        }else if((artwork.bidders).length > 0 && auction.owner !== artwork.owner){
            resolve({isSync: false})
            return
          }
        }
       
      } else {
        if (auction.active != artwork.isAuction) {
          resolve({isSync: false})
          return
        }
      }

      resolve({isSync: true})
      return
    }catch(err){
      reject(err)
      return
    }


    })
  }


  // const verifyAuction = async (artwork) => {
  //   try {
  //     let { active, seller, value, endTime, owner, currency } = await getAuction(
  //       artwork.tokenId,
  //     )
  //     if(!artwork.title || !artwork.description){
  //       dispatch(syncAuction({ tokenId: artwork.tokenId }))
  //     }

  //     if (active) {
  //       if (artworkSlice.status !== 'loading') {
  //       if (value !== artwork.price || endTime !== artwork.endTime || currency  !== artwork.currency ) {
  //           dispatch(syncAuction({ tokenId: artwork.tokenId }))
  //         }
  //         if((artwork.bidders).length > 0 && owner !== artwork.owner){
  //           dispatch(syncAuction({ tokenId: artwork.tokenId }))
  //         }
  //       }
       
  //     } else {
  //       if (active != artwork.isAuction) {
  //         dispatch(syncAuction({ tokenId: artwork.tokenId }))
  //       }
  //     }

  //     setArtworkAuction({
  //       active: active,
  //       seller: seller,
  //       value: value,
  //       endTime: endTime,
  //       owner: owner,
  //     })
  //   } catch (err) {
  //     setArtworkAuction({
  //       active: artwork.active,
  //       seller: artwork.seller,
  //       value: artwork.value,
  //       endTime: artwork.endTime,
  //       owner: artwork.owner,
  //     })
  //     console.log(err)
  //   }
  // }

  let price = artwork.price ? artwork.price : 0

  if (artworkAuction && artworkAuction.active) {
    price = artworkAuction.value
  }
  price = parseFloat(price) / 1e18

  const RightDetail = (artworkAuction) => {
    if (artworkAuction && artworkAuction.active) {
      return (
        <div className="counter">
          <h3 className="pb-2 pl-2">Auction ending in</h3>
          <div id="countdown1">
            <div className="well d-flex">
              <Countdown endTime={artworkAuction.endTime} />
            </div>
          </div>
        </div>
      )
    } else {
      return (
        <div className="center-100 ">
          <div>Owned By</div>
          <div className="rounded-link">
            <Link to={`/${artwork.owner}`}>
            {artwork.owner.slice(0, 8)}...
            </Link>
          </div>
        </div>
      )
    }
  }





  return (
    <>
      <div className="row">
        <div className="col-xl-6 border-right p-4">
          <div>
            <h3>Current Bid</h3>
          </div>
          <h2 className="font-weight-bold">{price} {artwork.currency}</h2>
          <h6 className="text-color pt-2">${(price * ETH_RATE).toFixed(2)}</h6>
        </div>
        <div className="col-xl-6">{RightDetail(artworkAuction)}</div>
      </div>
      <AuctionFooter artworkAuction={artworkAuction} artwork={artwork} />
    </>
  )
}

export default AuctionDetails
