import React from "react";
import { Link } from "react-router-dom";
import { useSelector } from "react-redux";
import ArtworkCard from "../../components/Card/ArtworkCard";

import { selectLiveArtworks } from "../../store/artwork/artworkSlice";

import ArtworksSkeleton from "./Skeleton/ArtworksSkeleton";

const Artworks = () => {
  const artworks = useSelector((state) => selectLiveArtworks(state));

  return (
    <section className="artwork">
      <div className="container">
        <div className="row">
          <div className="col-lg-12 pb-4">
            <h2 className="title1 float-left">Featured Artworks</h2>
            <Link to="/artworks">
              <button className="btn btn-primary float-right">
                View all Artwork
              </button>
            </Link>
          </div>
        </div>
      </div>
      {artworks && artworks.length >= 1 ? (
        <div className="container">
          <div className="masonry">
            {artworks.map((artwork) => {
              return (
                <ArtworkCard key={"art" + artwork._id} artwork={artwork} />
              );
            })}
          </div>
        </div>
      ) : (
        <ArtworksSkeleton />
      )}
    </section>
  );
};

export default Artworks;
