import React from 'react'
import { Link } from 'react-router-dom'

import { isVideo } from '../../utils'
import Clip from '../../components/Clip'

import { IMG_BASE } from '../../constant'
import CoverImg from '../../images/cover.jpg'
import AvatarImg from '../../images/avatar.png'


const CreatorCard = ({ creator }) => {
  const coverImg = creator.coverImg ? IMG_BASE + creator.coverImg : CoverImg
  const avatar = creator.avatar ? IMG_BASE + creator.avatar : AvatarImg

  return (
    <Link
      to={`/${creator.username ? creator.username : creator.publicAddress}`}
      className="card"
    >
      
      {isVideo(creator.coverImg) ? (
        <Clip  className=" card-img-top" url={coverImg} />
      ) : (
        <img className=" card-img-top" src={coverImg} />
      )}
      {isVideo(creator.avatar) ? (
        <Clip className="img-fluid creator-image-bige creator-avatar-img " url={avatar} />
      ) : (
        <img
          className="img-fluid creator-image-big creator-avatar-img " 
          src={avatar}
        />
      )}

      <div className="card-body">
        <h4 className="card-title">{creator.fullname? creator.fullname: 'unknown'}</h4>
        <h6 className="font-weight-bold mt-4 pb-2">@{creator.username? creator.username: (creator.publicAddress).slice(0,5)}</h6>
        <p className="card-text pt-3">{creator.bio}</p>
        <div className="know-more border-top">
          <div className="float-left mt-3">
            <button className="btn btn-primary">Follow</button>
          </div>
          <div className="float-right mt-3">
            <h3>{(creator.followers).length}</h3>
            <p> Followers</p>
          </div>
        </div>
      </div>
    </Link>
  )
}

